/*_________________Butto______________________ */
* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.btn-skapa {
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 55px;
    text-align: center;
    border-color: lightyellow;
    background-size: 300% 100%;

    border-radius: 50px;

    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn-skapa:hover {
    background-position: 100% 0;

    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}


.btn-skapa:hover {
    background-position: 100% 0;

    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn-skapa:focus {
    outline: none;
}

.btn-skapa.color-3 {
    background-image: linear-gradient(to right, #667eea, #764ba2, #6B8DD6, #8E37D7);
    box-shadow: 0 4px 15px 0 rgba(116, 79, 168, 0.75);
}

/*_________________End Button______________________ */

.input-Form {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: 100%;

}

.div-btn {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin-top: auto;
    width: 100%;

}

.btn-skapa:disabled {
    pointer-events: none;
    opacity: 0.70;
}

.input-url {
    width: 300px;
}