/*  Font fr√•m Google Font --- font-family: 'Roboto'*/
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap');

.App {
  display: flex;
  flex-direction: row;
  margin: 20px;
  padding: 20px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 20px;
}

.qrcode-input {
  display: flex;
  margin: 5px;
  padding: 5px;
  background-color: whitesmoke;
  flex-grow: 2;
  border-radius: 20px;
}

.qrcode-output {
  display: flex;
  text-align: center;
  justify-content: center;
  margin: 5px;
  padding: 5px;
  background-color: rgb(138, 143, 143);
  flex-grow: 1;
  border-radius: 20px;
}

/*-----------------------------------
============Mobile Screen============ 
-----------------------------------*/
@media only screen and (max-width: 900px) {

  .App {
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    margin: 5px;
    padding: 5px;
  }

  .qrcode-imput {
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;

  }

  .qrcode-output {
    display: flex;
    text-align: center;
    justify-content: center;

  }

  .div-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: auto;
    width: 100%;

  }

  .input-url {
    width: auto;
  }

}

/*-----------------------------------
==========End Mobile Screen========== 
------------------------------------*/

.error-message {
  display: flex;
  display: flex;
  align-items: center;
  justify-content: center;
  color: orange;
  font-size: 20px;
  font-weight: 700;
}

.labelett{
  font-size: 20px;
  font-weight: 700;
  color: black;
}

@media only screen and (min-width: 901px) {

  .App {
    display: flex;    
    flex-direction: row;    
  }

}